const slidingInputs = document.querySelectorAll(".sliding-input input");
const togglePassword = document.querySelectorAll(".toggle-password");

if (togglePassword)
  togglePassword.forEach((p) =>
    p.addEventListener("click", function (e) {
      handlePassword(this, e);
    })
  );

if (slidingInputs)
  slidingInputs.forEach((i) => {
    const parentElem = i.parentElement;
    if (i.value && i.value !== "") parentElem.classList.add("focus");

    Array.from(parentElem.children).forEach((c) => {
      if (c.className.includes("prefix"))
        parentElem.classList.add("input-prefix");
    });

    i.addEventListener("focusin", handleFocus);
    i.addEventListener("focusout", handleFocus);
  });

function handleFocus() {
  if (!this.value) this.parentElement.classList.toggle("focus");
}

function handlePassword(ref, e) {
  e.preventDefault();
  if (ref.nextElementSibling.type === "text")
    ref.nextElementSibling.type = "password";
  else ref.nextElementSibling.type = "text";
  ref.classList.toggle("active");
}
